// tab style

@import "./variable";
@import "../../theme/variable";

@import "../../theme/mixin";
// @import "./mixin";

//  flex  display table 
.tab-bar {
  position: relative;
  display: flex;
  flex-direction: row;
  text-transform: uppercase;
}

.tab-bar-left {
  justify-content: flex-start;
}

.tab-bar-center {
  justify-content: center;
}

.tab-bar-right {
  justify-content: flex-end;
}

.tab-bar-between {
  justify-content: space-between;

  .tab {
    flex: 1;
  }
}

.tab {
  position: relative;
  display: inline-block;
  padding: $tab-padding-x $tab-padding-y;
  overflow: hidden;
  font-size: $tab-font-size;
  font-weight: $tab-font-weight;
  line-height: $tab-line-height;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;

  @include theme-prop(color, secondary-on-light);

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    display: none;
    width: calc(100% - #{$tab-active-width-subtract});
    height: $tab-active-border;
    background-color: transparent;
    pointer-events: none;
  }

  &.active {
    @include theme-prop(color, primary-on-light);

    // 
    &::after {
      display: block;
      @include theme-prop(background-color, primary-on-light);
    }
  }

  &:hover {
    text-decoration: none;
  }

  &:focus {
    outline: 0;
  }

  &:focus::after,
  &:hover::after {
    opacity: .38;
  }

  &.active::after,
  &:active::after {
    opacity: .87;
  }
}

//   TODO  js  
.tab-ripple .tab {

  &::after {
    transform: scale(0);
  }
  &.active::after {
    transform: scale(1);
    transition: transform 240ms 0ms cubic-bezier(.5, -.5, 1, 1);
  }
}

//  tab
.tab-bar-sm .tab {
  font-size: $tab-font-size-sm;
  font-weight: $tab-font-weight-sm;
}

