// 
@import "../../base/mixins/hover";
@import "../../theme/mixin";
@import "../../responsive/mixin";
@import "../../animation/mixin";
@import "../../shadow/mixin";
@import "../../ripple/mixin";
@import "variable";

// 
.navbar {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap; // 
  align-items: center;
  justify-content: space-between;
  height: $navbar-height;
  padding-left: $navbar-padding-x;
  padding-right: $navbar-padding-x;

  & * {
    box-sizing: border-box;
  }
}

// Navbar brand
//  brand, project, or site 
.navbar-brand {
  display: inline-flex;
  padding-top: $navbar-brand-padding-y;
  padding-bottom: $navbar-brand-padding-y;
  margin-right: $navbar-brand-margin-right;
  font-size: $navbar-font-size;
  line-height: inherit;
  white-space: nowrap;
  @include theme-prop(color, primary-on-light);

  @include hover-focus {
    text-decoration: none;
    @include theme-prop(color, primary-on-light);
  }
}

// 
//  .navbar-expand-x 
.navbar-toggler {
  padding: $navbar-toggler-padding-y $navbar-toggler-padding-x;
  border: 0; // remove default button style
  font-size: $navbar-font-size;
  line-height: 1;
  background: transparent; // remove default button style
  cursor: pointer;

  &:focus {
    outline: none;
  }

  @include hover-focus {
    text-decoration: none;
  }
}

// 
.navbar-toggler-icon {
  position: relative;
  display: inline-block;
  width: 1.2em;
  height: .9em;
  border-top: .15em solid currentColor;
  border-bottom: .15em solid currentColor;

  &::after {
    content: "";
    position: absolute;
    top: .24em;
    left: 0;
    right: 0;
    border-top: .15em solid currentColor;
  }
}

// 
.navbar-collapse {
  display: none;
  flex-basis: 100%;
  align-items: center;
  opacity: 0;

  //  fixme 
  transition: animation-enter(opacity, 120ms);
  //  show 
  &.show {
    display: block;
    opacity: 1;
  }
}

//  nav-link 
.navbar-nav {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
  margin-left: 0;
  font-size: $navbar-font-size;
  list-style: none;

  .nav-link {
    display: inline-flex;
    padding: 1rem 1.25rem;
    margin-right: .25rem;
    border-bottom: 2px solid transparent;
    cursor: pointer;
    @include theme-prop(color, secondary-on-light);

    @include hover-focus {
      text-decoration: none;
      @include theme-prop(color, primary-on-light);
      @include theme-prop(border-bottom-color, secondary-on-light);
    }

    &.active {
      @include theme-prop(color, primary-on-light);
      @include theme-prop(border-bottom-color, primary-on-light);
    }

    &.disabled {
      border-bottom-color: transparent;
      cursor: default;
      @include theme-prop(color, disabled-on-light);
    }
  }
}

// 
// .navbar-expand .navbar-expand-sm .navbar-expand-md .navbar-expand-lg .navbar-expand-xl
//  .navbar-expand 
//  .navbar-expand-sm 600px 
.navbar-expand {
  @each $breakpoint in map-keys($responsive-breakpoints) {
    $next: breakpoint-next($breakpoint, $responsive-breakpoints);
    $infix: breakpoint-infix($next, $responsive-breakpoints);

    &#{$infix} {
      @include media-breakpoint-up($next) {
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-start;

        .navbar-nav {
          flex-direction: row;
        }

        .navbar-collapse {
          display: flex;
          opacity: 1;
        }

        .navbar-toggler {
          display: none;
          &.show {
            display: block;
          }
        }
      }
    }
  }
}

//  fixed 
.navbar-fixed {
  position: fixed;
  top: 0;
  left: 0;
  // todo z-index 
  z-index: 4;
  width: 100%;
  @include shadow(4);
}

//  fixed  navbar  margin-top
.navbar-fixed-adjust {
  margin-top: $navbar-height;
}

// 
@each $style in ("primary", "secondary") {
  .navbar-#{$style} {
    @include theme-prop(background-color, #{$style});

    .navbar-toggler {
      @include theme-prop(color, primary-on-dark);
    }

    .navbar-brand {
      @include theme-prop(color, primary-on-dark);
      @include hover-focus {
        @include theme-prop(color, primary-on-dark);
      }
    }

    .navbar-nav {
      .nav-link {
        @include theme-prop(color, secondary-on-dark);

        @include hover-focus {
          text-decoration: none;
          @include theme-prop(color, primary-on-dark);
          @include theme-prop(border-bottom-color, secondary-on-dark);
        }

        &.active {
          @include theme-prop(color, primary-on-dark);
          @include theme-prop(border-bottom-color, primary-on-dark);
        }

        &.disabled {
          border-bottom-color: transparent;
          cursor: default;
          @include theme-prop(color, disabled-on-dark);
        }
      }
    }
    // ripple 
    .nav-link.ripple {
      &::before,
      &::after {
        // ripple 
        @include ripple-theme((theme-style: primary-on-#{$style}, opacity: .16));
      }
    }
  }
}


