// 
@import "./color_palette";
@import "../base/variables/options";
@import "./function";

// 
//  CSS 
$theme-primary: #2196f3 !default; //  $blue-500 #2196f3
$theme-secondary: #e040fb !default; // (accent) $purple-a-200 #e040fb
$theme-background: #fff !default; // 

// 
$theme-primary-light: #64b5f6 !default; //  $blue-300
$theme-secondary-light: #ea80fc !default; //  $purple-a-100

// 
$theme-primary-lighter: #bbdefb !default; //  $blue-100
$theme-secondary-lighter: #e1bee7 !default; //  $purple-100

// 
$theme-primary-lightest: #e3f2fd !default; //  $blue-50
$theme-secondary-lightest: #f3e5f5 !default; //  $purple-50

// 
$theme-primary-dark: #1976d2 !default; //  $blue-700
$theme-secondary-dark: #d500f9 !default; //  $purple-a-400

// 
$theme-primary-darker: #1565c0 !default; //  $blue-800
$theme-secondary-darker: #aa00ff !default; //  $purple-a-700

// 
$theme-primary-darkest: #0d47a1 !default; //  $blue-900
$theme-secondary-darkest: #4a148c !default; //  $purple-900

//  light or dark
$theme-primary-tone: theme-light-or-dark($theme-primary);
$theme-secondary-tone: theme-light-or-dark($theme-secondary);

// 
$theme-primary-light-tone: theme-light-or-dark($theme-primary-light);
$theme-secondary-light-tone: theme-light-or-dark($theme-secondary-light);

// 
$theme-primary-lighter-tone: theme-light-or-dark($theme-primary-lighter);
$theme-secondary-lighter-tone: theme-light-or-dark($theme-secondary-lighter);

// 
$theme-primary-lightest-tone: theme-light-or-dark($theme-primary-lightest);
$theme-secondary-lightest-tone: theme-light-or-dark($theme-secondary-lightest);

// 
$theme-primary-dark-tone: theme-light-or-dark($theme-primary-dark);
$theme-secondary-dark-tone: theme-light-or-dark($theme-secondary-dark);

// 
$theme-primary-darker-tone: theme-light-or-dark($theme-primary-darker);
$theme-secondary-darker-tone: theme-light-or-dark($theme-secondary-darker);

// 
$theme-primary-darkest-tone: theme-light-or-dark($theme-primary-darkest);
$theme-secondary-darkest-tone: theme-light-or-dark($theme-secondary-darkest);

// 
$theme-background-tone: theme-light-or-dark($theme-background);

// 
//  disabled readonly 
$theme-text-colors: (
  dark: (
    primary: rgba(0, 0, 0, .87),
    secondary: rgba(0, 0, 0, .54),
    hint: rgba(0, 0, 0, .38),
    disabled: rgba(0, 0, 0, .38)
  ),
  light: (
    primary: #ffffff,
    secondary: rgba(255, 255, 255, .7),
    hint: rgba(255, 255, 255, .5),
    disabled: rgba(255, 255, 255, .5)
  )
);

// 
$theme-property-values: (
  // 
  primary: $theme-primary,
  secondary: $theme-secondary,
  background: $theme-background,

  // 
  primary-light: $theme-primary-light,
  secondary-light: $theme-secondary-light,

  // 
  primary-lighter: $theme-primary-lighter,
  secondary-lighter: $theme-secondary-lighter,

  // 
  primary-lightest: $theme-primary-lightest,
  secondary-lightest: $theme-secondary-lightest,

  // 
  primary-dark: $theme-primary-dark,
  secondary-dark: $theme-secondary-dark,

  // 
  primary-darker: $theme-primary-darker,
  secondary-darker: $theme-secondary-darker,

  // 
  primary-darkest: $theme-primary-darkest,
  secondary-darkest: $theme-secondary-darkest,
) !default;

// 
$theme-tone-property-values: (
  //  background-color border-color  color 
  //  map-get($theme-text-colors, $theme-primary-tone)  light 
  //  map-get($theme-text-colors, $theme-primary-tone)  dark 

  //  $theme-primary  primary secondary hint  disabled 
  primary-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), primary),
  secondary-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), secondary),
  hint-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), hint),
  disabled-on-primary: map-get(map-get($theme-text-colors, $theme-primary-tone), disabled),

  //  $theme-primary-light  primary secondary hint  disabled 
  primary-on-primary-light: map-get(map-get($theme-text-colors, $theme-primary-light-tone), primary),
  secondary-on-primary-light: map-get(map-get($theme-text-colors, $theme-primary-light-tone), secondary),
  hint-on-primary-light: map-get(map-get($theme-text-colors, $theme-primary-light-tone), hint),
  disabled-on-primary-light: map-get(map-get($theme-text-colors, $theme-primary-light-tone), disabled),

  //  $theme-primary-lighter  primary secondary hint  disabled 
  primary-on-primary-lighter: map-get(map-get($theme-text-colors, $theme-primary-lighter-tone), primary),
  secondary-on-primary-lighter: map-get(map-get($theme-text-colors, $theme-primary-lighter-tone), secondary),
  hint-on-primary-lighter: map-get(map-get($theme-text-colors, $theme-primary-lighter-tone), hint),
  disabled-on-primary-lighter: map-get(map-get($theme-text-colors, $theme-primary-lighter-tone), disabled),

  //  $theme-primary-lightest  primary secondary hint  disabled 
  primary-on-primary-lightest: map-get(map-get($theme-text-colors, $theme-primary-lightest-tone), primary),
  secondary-on-primary-lightest: map-get(map-get($theme-text-colors, $theme-primary-lightest-tone), secondary),
  hint-on-primary-lightest: map-get(map-get($theme-text-colors, $theme-primary-lightest-tone), hint),
  disabled-on-primary-lightest: map-get(map-get($theme-text-colors, $theme-primary-lightest-tone), disabled),

  //  $theme-primary-dark  primary secondary hint  disabled 
  primary-on-primary-dark: map-get(map-get($theme-text-colors, $theme-primary-dark-tone), primary),
  secondary-on-primary-dark: map-get(map-get($theme-text-colors, $theme-primary-dark-tone), secondary),
  hint-on-primary-dark: map-get(map-get($theme-text-colors, $theme-primary-dark-tone), hint),
  disabled-on-primary-dark: map-get(map-get($theme-text-colors, $theme-primary-dark-tone), disabled),

  //  $theme-primary-darker  primary secondary hint  disabled 
  primary-on-primary-darker: map-get(map-get($theme-text-colors, $theme-primary-darker-tone), primary),
  secondary-on-primary-darker: map-get(map-get($theme-text-colors, $theme-primary-darker-tone), secondary),
  hint-on-primary-darker: map-get(map-get($theme-text-colors, $theme-primary-darker-tone), hint),
  disabled-on-primary-darker: map-get(map-get($theme-text-colors, $theme-primary-darker-tone), disabled),

  //  $theme-primary-darkest  primary secondary hint  disabled 
  primary-on-primary-darkest: map-get(map-get($theme-text-colors, $theme-primary-darkest-tone), primary),
  secondary-on-primary-darkest: map-get(map-get($theme-text-colors, $theme-primary-darkest-tone), secondary),
  hint-on-primary-darkest: map-get(map-get($theme-text-colors, $theme-primary-darkest-tone), hint),
  disabled-on-primary-darkest: map-get(map-get($theme-text-colors, $theme-primary-darkest-tone), disabled),

  //  $theme-secondary  primary secondary hint  disabled 
  primary-on-secondary: map-get(map-get($theme-text-colors, $theme-secondary-tone), primary),
  secondary-on-secondary: map-get(map-get($theme-text-colors, $theme-secondary-tone), secondary),
  hint-on-secondary: map-get(map-get($theme-text-colors, $theme-secondary-tone), hint),
  disabled-on-secondary: map-get(map-get($theme-text-colors, $theme-secondary-tone), disabled),

  //  $theme-secondary-light  primary secondary hint  disabled 
  primary-on-secondary-light: map-get(map-get($theme-text-colors, $theme-secondary-light-tone), primary),
  secondary-on-secondary-light: map-get(map-get($theme-text-colors, $theme-secondary-light-tone), secondary),
  hint-on-secondary-light: map-get(map-get($theme-text-colors, $theme-secondary-light-tone), hint),
  disabled-on-secondary-light: map-get(map-get($theme-text-colors, $theme-secondary-light-tone), disabled),

  //  $theme-secondary-lighter  primary secondary hint  disabled 
  primary-on-secondary-lighter: map-get(map-get($theme-text-colors, $theme-secondary-lighter-tone), primary),
  secondary-on-secondary-lighter: map-get(map-get($theme-text-colors, $theme-secondary-lighter-tone), secondary),
  hint-on-secondary-lighter: map-get(map-get($theme-text-colors, $theme-secondary-lighter-tone), hint),
  disabled-on-secondary-lighter: map-get(map-get($theme-text-colors, $theme-secondary-lighter-tone), disabled),

  //  $theme-secondary-lightest  primary secondary hint  disabled 
  primary-on-secondary-lightest: map-get(map-get($theme-text-colors, $theme-secondary-lightest-tone), primary),
  secondary-on-secondary-lightest: map-get(map-get($theme-text-colors, $theme-secondary-lightest-tone), secondary),
  hint-on-secondary-lightest: map-get(map-get($theme-text-colors, $theme-secondary-lightest-tone), hint),
  disabled-on-secondary-lightest: map-get(map-get($theme-text-colors, $theme-secondary-lightest-tone), disabled),

  //  $theme-secondary-dark  primary secondary hint  disabled 
  primary-on-secondary-dark: map-get(map-get($theme-text-colors, $theme-secondary-dark-tone), primary),
  secondary-on-secondary-dark: map-get(map-get($theme-text-colors, $theme-secondary-dark-tone), secondary),
  hint-on-secondary-dark: map-get(map-get($theme-text-colors, $theme-secondary-dark-tone), hint),
  disabled-on-secondary-dark: map-get(map-get($theme-text-colors, $theme-secondary-dark-tone), disabled),

  //  $theme-secondary-darker  primary secondary hint  disabled 
  primary-on-secondary-darker: map-get(map-get($theme-text-colors, $theme-secondary-darker-tone), primary),
  secondary-on-secondary-darker: map-get(map-get($theme-text-colors, $theme-secondary-darker-tone), secondary),
  hint-on-secondary-darker: map-get(map-get($theme-text-colors, $theme-secondary-darker-tone), hint),
  disabled-on-secondary-darker: map-get(map-get($theme-text-colors, $theme-secondary-darker-tone), disabled),

  //  $theme-secondary-darkest  primary secondary hint  disabled 
  primary-on-secondary-darkest: map-get(map-get($theme-text-colors, $theme-secondary-darkest-tone), primary),
  secondary-on-secondary-darkest: map-get(map-get($theme-text-colors, $theme-secondary-darkest-tone), secondary),
  hint-on-secondary-darkest: map-get(map-get($theme-text-colors, $theme-secondary-darkest-tone), hint),
  disabled-on-secondary-darkest: map-get(map-get($theme-text-colors, $theme-secondary-darkest-tone), disabled),

  //  $theme-background  primary secondary hint  disabled 
  primary-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), primary),
  secondary-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), secondary),
  hint-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), hint),
  disabled-on-background: map-get(map-get($theme-text-colors, $theme-background-tone), disabled),

  //  primary secondary hint  disabled 
  primary-on-light: map-get(map-get($theme-text-colors, dark), primary),
  secondary-on-light: map-get(map-get($theme-text-colors, dark), secondary),
  hint-on-light: map-get(map-get($theme-text-colors, dark), hint),
  disabled-on-light: map-get(map-get($theme-text-colors, dark), disabled),

  //  primary secondary hint  disabled 
  primary-on-dark: map-get(map-get($theme-text-colors, light), primary),
  secondary-on-dark: map-get(map-get($theme-text-colors, light), secondary),
  hint-on-dark: map-get(map-get($theme-text-colors, light), hint),
  disabled-on-dark: map-get(map-get($theme-text-colors, light), disabled)
) !default;

//  success  info warning  error 
// 
$theme-assist: ("success", "info", "warning", "error") !default;

// 
$theme-assist-colors: (
  success: #4caf50, // $green-500
  info: #00bcd4, // $cyan-500
  warning: #ff9800, // $orange-500
  error: #f44336, // $red-500

  // 
  success-light: #c8e6c9, // $green-100
  info-light: #b2ebf2, // $cyan-100
  warning-light: #ffe0b2, // $orange-100
  error-light: #ffcdd2, // $red-100

  // 
  success-dark: #388e3c, // $green-700
  info-dark: #0097a7, // $cyan-700
  warning-dark: #f57c00, // $orange-700
  error-dark: #d32f2f, // $red-700
) !default;

//  $enable-transparent 
//  base/variables/_options.scss  $enable-transparent 

//  css 
$greys: (
  50: #fafafa,
  100: #f5f5f5,
  200: #eeeeee,
  300: #e0e0e0,
  400: #bdbdbd,
  500: #9e9e9e,
  600: #757575,
  700: #616161,
  800: #424242,
  900: #212121
) !default;

// 
$greys-transparent: (
  50: rgba(0, 0, 0, .02), //  #fafafa;
  100: rgba(0, 0, 0, .04), //  #f5f5f5
  200: rgba(0, 0, 0, .065), //  #eeeeee
  300: rgba(0, 0, 0, .12), //  #e0e0e0
  400: rgba(0, 0, 0, .26), //  #bdbdbd
  500: rgba(0, 0, 0, .38), //  #9e9e9e
  600: rgba(0, 0, 0, .54), //  #757575
  700: rgba(0, 0, 0, .62), //  #616161
  800: rgba(0, 0, 0, .74), //  #424242
  900: rgba(0, 0, 0, .87) //  #212121
) !default;

// 
$white: #ffffff !default;
$black: #000000 !default;
$transparent: rgba(0, 0, 0, 0) !default; // 

$grey-color-50: if($enable-transparent, map-get($greys-transparent, 50), map-get($greys, 50)) !default;
$grey-color-100: if($enable-transparent, map-get($greys-transparent, 100), map-get($greys, 100)) !default;
$grey-color-200: if($enable-transparent, map-get($greys-transparent, 200), map-get($greys, 200)) !default;
$grey-color-300: if($enable-transparent, map-get($greys-transparent, 300), map-get($greys, 300)) !default;
$grey-color-400: if($enable-transparent, map-get($greys-transparent, 400), map-get($greys, 400)) !default;
$grey-color-500: if($enable-transparent, map-get($greys-transparent, 500), map-get($greys, 500)) !default;
$grey-color-600: if($enable-transparent, map-get($greys-transparent, 600), map-get($greys, 600)) !default;
$grey-color-700: if($enable-transparent, map-get($greys-transparent, 700), map-get($greys, 700)) !default;
$grey-color-800: if($enable-transparent, map-get($greys-transparent, 800), map-get($greys, 800)) !default;
$grey-color-900: if($enable-transparent, map-get($greys-transparent, 900), map-get($greys, 900)) !default;
